<template>
  <div
    v-if="cart_data.length"
    class="v-cart">
    <div class="v-cart__bag">
      <div class="v-cart__price-wrap">
        <div class="v-cart__price">
          <p
            class="v-cart__price-total">
            = {{calcTotalPrice()}} ₴
          </p>
          <span class="v-cart__price-triangle"></span>
        </div>
      </div>
      <span class="v-cart__number">
        {{cart_data.length}}
      </span>
    </div>
  </div>
  <div
    v-else
    class="v-cart">
    <div class="v-cart__bag">
      <div class="v-cart__price-wrap">
        <div class="v-cart__price">
          <p class="v-cart__price-total">= 0 ₴</p>
          <span class="v-cart__price-triangle"></span>
        </div>
      </div>
      <span class="v-cart__number">0</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'v-cart',
    components: {},
    props: {
      cart_data: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {}
    },
    computed: {},
    methods: {
      calcTotalPrice() {
        let totalPrice = 0;
        for (let i = 0; i < this.cart_data.length; i++) {
          let newPrice = +this.cart_data[i].price.replace(/[^0-9]/g,"");
          totalPrice = totalPrice + newPrice;
        }
        return totalPrice;
      }
    },
    watch: {},
    mounted() {}
  }
</script>

<style lang="scss">
  .v-cart {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 15px;
    margin: 0;
    color: rgb(57, 61, 61);
    &__bag {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background-color: $white;
      background-image: url('../assets/images/icons/cart.png');
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 40px;
      box-shadow: 0px 0px 30px 0px rgba(205, 71, 41, 0.26);
      position: relative;
    }
    &__price {
      position: relative;
      background-color: rgb(57, 61, 61);
      width: 100px;
      height: 40px;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      &-wrap {
        position: absolute;
        left: -125px;
        top: calc(50% - 20px);
      }
      &-total {
        margin: 0;
        font-weight: 600;
        font-size: 16px;
        color: $white;
      }
      &-triangle {
        display: block;
        height: 20px;
        width: 20px;
        background-color: inherit;
        border: inherit;
        position: absolute;
        top: calc(50% - 10px);
        right: -9px;
        clip-path: polygon(50% 0%, 50% 100%, 100% 50%);
        border-radius: 0 0 0 0.25em;
      }
    }
    &__number {
      position: absolute;
      bottom: -10px;
      right: -10px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 35px;
      height: 35px;
      border-radius: 50%;
      background-color: $orange;
      color: $white;
      font-weight: 600;
      font-size: 16px;
    }
  }
  @media screen and ( max-width: 992px ) {
    .v-cart {
      padding-right: 25px;
      &__bag {
        width: 60px;
        height: 60px;
        background-size: 30px;
      }
      &__price {
        width: 80px;
        height: 30px;
        border-radius: 5px;
        &-wrap {
          position: absolute;
          left: -100px;
          top: calc(50% - 15px);
        }
        &-total {
          font-size: 14px;
        }
        &-triangle {
          height: 15px;
          width: 15px;
          top: calc(50% - 7px);
          right: -7px;
        }
      }
      &__number {
        bottom: -10px;
        right: -10px;
        width: 30px;
        height: 30px;
        font-size: 14px;
      }
    }
  }
  @media screen and (max-width: 576px) {
    .v-cart {
      margin-bottom: 20px;
      padding-right: 40px;
    }
  }
</style>